import React from 'react';
import GoogleLogin from 'react-google-login';
import Button from './Button';

export default function GoogleLoginHandler({text}) {
    const responseGoogle = (response) => {
        console.log(response);
    };

    return (
        <GoogleLogin
            render={(renderProps) => <Button text={text}  onClick={renderProps.onClick} disabled={renderProps.disabled} />}
            clientId="178683516706-dskp14ak0s9vivcnauhs78ur84gl008c.apps.googleusercontent.com"
            buttonText="Login"
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
            cookiePolicy={'single_host_origin'}
        />
    );
}
