/* eslint-disable react/prop-types */
import React from 'react';
import Background from './assets/frame.png';
import Vector from './assets/vector.png';
import './index.css';

const Body = ({ children, description }) => {
    return (
        <div className="container content">
            <div className="jumbion_container" style={{ backgroundImage: `url(${Background})` }}>
                <div className="jumbion_container_adds" style={{ backgroundImage: `url(${Vector})` }}></div>
            </div>
            <div className="content_desc">
                <p>
                    Scan For <span className="text_default_color text_bold">FRAUDULENT</span>
                </p>
                <p className="text_bold">Documents</p>
            </div>
            <div className="content_wrapper">
                <div className="content_wrapper_box">
                    <div className="content_body">{children}</div>
                    {description && <div className="box_description">{description}</div>}
                </div>
            </div>
        </div>
    );
};

export default Body;
