import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    position: relative;
    margin-bottom: 30px;
    width: 100%;
`;
const Label = styled.div`
    margin-bottom: 10px;
    font-weight: 700;
`;

const Importants = styled.div`
    margin: 2px;
    color: #ff6600;
    display: inline-block;
    font-weight: bold;
`;

const Input = styled.input`
    width: 100%;
    padding: 10px;
    border-radius: 6px;
    height: 50px;
    background: ${({disabled}) => disabled ? '#F3F2F7' : '#f0f7fc'}
`;

const TextArea = styled.textarea`
    width: 100%;
    padding: 10px;
    border-radius: 6px;
    min-height: 100px;
    max-height: 150px;
`;

export default function TextInputs({ title, important, textarea, ...props }) {
    return (
        <Container>
            <Label>
                {title} {important && <Importants>*</Importants>}
            </Label>
            {textarea ? <TextArea {...props} /> : <Input {...props} />}
        </Container>
    );
}
