import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import Select from 'react-select';

import './index.css';

const Container = styled.div`
    position: relative;
    margin-bottom: 30px;
    width: 100%;
`;
const Label = styled.div`
    margin-bottom: 10px;
    font-weight: 700;
`;

const Importants = styled.div`
    margin: 2px;
    color: #ff6600;
    display: inline-block;
    font-weight: bold;
`;

const options = [{ value: 'chocolate', label: 'Chocolate' }];

export default function Selects({ children, important, title, ...props }) {
    const customStyles = {
        option: (provided, state) => ({
            ...provided,
            color: colors.textColor,
            backgroundColor: state.isFocused ? 'rgb(240, 247, 252)' : colors.backgroundColor,
        }),
    };

    return (
        <Container>
            <Label>
                {title} {important && <Importants>*</Importants>}
            </Label>
            <Select
                name="colors"
                options={options}
                className="basic-multi-select"
                classNamePrefix="select"
                style={{ height: '50px', width: '100%' }}
                styles={customStyles}
                {...props}
            />
        </Container>
    );
}
